<script lang="ts">
  import { createEventDispatcher } from 'svelte'
  import { Modal, Form, Slider, Button } from 'spaper'
  import { handleTxWithUx } from '../helpers/tx-helper'
  import { withdraw } from '../helpers/eth-helper'

  export let pid: number
  export let balance: number

  const dispatch = createEventDispatcher()

  let value = 0

  async function withdrawHandler() {
    console.log(pid, value)
    await handleTxWithUx(
      'Withdraw',
      withdraw, [pid, value]
    )
  }

  function onclose() {
    value = 0
    dispatch('close')
  }
</script>

<Modal active={pid !== null} title="Withdraw"
       on:close={onclose}>
  <Form>
    <Slider bind:value max={balance} step={1/1e18} block />
  </Form>
  <svelte:fragment slot="footer">
    <Button type="warning"
            on:click={() => value = balance}>
      Max
    </Button>
    <Button on:click={withdrawHandler}>
      Withdraw
    </Button>
  </svelte:fragment>
</Modal>
