import { derived, writable } from 'svelte/store';
import { getConnectedWallet, getChain, provider, getEthBalance } from './helpers/eth-helper';
export const selectedAccount = writable(null);
export const userBalance = writable(null);
export const chainId = writable(null);
export const hasWallet = writable(true);
export const showTxModal = writable(false);
export const pendingTx = writable(false);
export const txHash = writable(null);
export const txTitle = writable('');
const BSC_ID = 56;
export const connected = derived(selectedAccount, $selectedAccount => !!$selectedAccount);
export const rightNetwork = derived(chainId, $chainId => $chainId === BSC_ID);
// Can't derived from derived
export const userReady = derived([selectedAccount, chainId], ([$selectedAccount, $chainId]) => $chainId === BSC_ID && !!$selectedAccount);
(async () => {
    if (!window.ethereum) {
        hasWallet.set(false);
        return;
    }
    // Can't be done through provider as far as I know
    window.ethereum.on('accountsChanged', async ([account]) => {
        selectedAccount.set(account);
        userBalance.set(await getEthBalance(account));
    });
    provider.on('block', (_blockNumber) => {
        selectedAccount.subscribe(async (val) => {
            if (val)
                userBalance.set(await getEthBalance(val));
        });
    });
    provider.on('network', (_network, previousNetwork) => {
        if (previousNetwork) {
            window.location.reload();
        }
    });
    selectedAccount.set(await getConnectedWallet());
    chainId.set(await getChain());
})();
