import axios from 'axios';
export async function getPrice(id) {
    try {
        const { data } = await axios(`https://api.coingecko.com/api/v3/simple/price?ids=${id}&vs_currencies=usd`);
        return data[id].usd;
    }
    catch (_a) {
        return null;
    }
}
export const getPantherPrice = getPrice.bind(null, 'pantherswap');
