import { showTxModal, pendingTx, txHash, txTitle } from '../store';
import { provider } from './eth-helper';
import { Toast } from 'spaper';
async function doTransaction(tx, params = []) {
    try {
        const result = await tx.apply(null, params);
        return [result, true];
    }
    catch (error) {
        return [parseErrorMessage(error), false];
    }
}
export async function handleTxWithUx(title, tx, params = [], onEnd) {
    showTxModal.set(true);
    txTitle.set(title);
    const [data, success] = await doTransaction(tx, params);
    return new Promise((resolve, reject) => {
        if (!success) {
            showTxModal.set(false);
            const { code } = data;
            reject(code);
            if (code === 4001) { // Denied by user
                return;
            }
            // TODO: use .error when available
            return Toast.danger({
                message: `Une erreur est survenue. ${data}`,
                indefinite: true
            });
        }
        const hash = data.hash;
        txHash.set(hash);
        pendingTx.set(true);
        provider.once(hash, async (t) => {
            Toast.success('Transaction confirme !');
            pendingTx.set(false);
            showTxModal.set(false);
            resolve(t);
            await (onEnd === null || onEnd === void 0 ? void 0 : onEnd());
        });
    });
}
function parseErrorMessage(error) {
    const { code, message, data } = error;
    if (code === 'INSUFFICIENT_FUNDS')
        return 'Fonds insuffisants.';
    if (data.message) {
        return data.message;
    }
    if (typeof code === 'number')
        return error;
    try {
        return JSON.parse(message.substring(message.indexOf('{'), message.indexOf('}}}') + 3));
    }
    catch (_a) {
        return error;
    }
}
