import { providers, Contract, utils } from 'ethers';
import { MasterchefContract } from '../contracts';
const providerURI = 'https://rpc.ankr.com/bsc';
const readProvider = new providers.JsonRpcProvider(providerURI);
const contract = new Contract(MasterchefContract.address, MasterchefContract.abi, readProvider);
let provider;
let contractWithSigner;
let signer;
const referrer = '0xc8af4B1C97c0141a08F96301d0e1A49D5423DD26';
(() => {
    if (!window.ethereum)
        return;
    provider = new providers.Web3Provider(window.ethereum, 'any');
    signer = provider.getSigner();
    contractWithSigner = new Contract(MasterchefContract.address, MasterchefContract.abi, provider).connect(signer);
})();
export async function connectWallet() {
    try {
        const [account] = await provider.send('eth_requestAccounts', []);
        return account;
    }
    catch (error) {
        return null;
    }
}
export async function getConnectedWallet() {
    try {
        return await signer.getAddress();
    }
    catch (error) {
        return null;
    }
}
export async function getEthBalance(account) {
    try {
        const balance = await provider.getBalance(account);
        return fromWei(balance.toHexString());
    }
    catch (error) {
        return 0;
    }
}
export async function getChain() {
    try {
        const { chainId } = await provider.getNetwork();
        return chainId;
    }
    catch (error) {
        return null;
    }
}
export function toWei(amount) {
    // ethers amount to wei in hexadecimal format
    return utils.parseEther(String(amount)).toHexString();
}
export function fromWei(amount) {
    // hexadecimal wei amount to ethers
    return Number(utils.formatEther(amount));
}
export async function getPoolInfo(pid) {
    return contract.poolInfo(pid);
}
async function _getUserLPBalance(pid, user) {
    const { amount } = await contract.userInfo(pid, user);
    return amount;
}
export async function getUserLPBalance(pid, user) {
    const balance = await _getUserLPBalance(pid, user);
    return fromWei(balance);
}
export async function getPoolCount() {
    return (await contract.poolLength()).toNumber();
}
export async function getPendingRewards(pid, user) {
    return fromWei(await contract.pendingPanther(pid, user));
}
export async function harvest(pid) {
    // Harvest is depositing 0
    return contractWithSigner.deposit(pid, 0, referrer);
}
export async function withdraw(pid, amount) {
    return contractWithSigner.withdraw(pid, toWei(amount));
}
export async function withdrawAll(pid, user) {
    const userBalance = _getUserLPBalance(pid, user);
    return contractWithSigner.withdraw(pid, userBalance);
}
export { provider };
